import React, { Component } from 'react';
import InitialState from '../../data/initialState';

class Feed extends Component {
    constructor(props) {
        super(props)
        this.state = { feeds: InitialState.sections.feeds }
    }

    render(){
        return (
            <section className="text-center feed-section" id="feeds">
                <div className="container">
                <h1 className="mb-5 text-accent"><i className="fas fa-broadcast-tower"></i> &nbsp;Feeds</h1>
                <p className="text-left">I like to attend workshops and meetups, it helps me meet new people and also learn a lot through their experiences. I also get to know about new tech and brush up my existing skills in the technologies that I use.</p>
                <h3 className="text-left mt-5">Recent Workshops</h3>
                <hr className="hr-almost-black pad-0 margin-0 mb-2"></hr>
                <div className="row">
                    {this.state.feeds.map((feed,i) =>
                        <div className="col-12 col-md-4 mt-4 text-black text-left" key={i}>
                            <h4>{feed.title}</h4>
                            <p>{feed.text}</p>
                            <p className="margin-0"><b>Venue:</b> {feed.venue}</p>
                            <p><b>Date:</b> {feed.date}</p>
                            <a href={feed.link} target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i> Link</a>
                        </div>
                    )}
                </div>
                </div>
                {/* <div id="carouselExampleControls" className="carousel slide container" data-ride="carousel">
                    <div className="carousel-inner">
                        {this.state.feeds.map((feed,i) =>
                            <div className={i === 0 ? "carousel-item active" : "carousel-item"}>
                            { i%2 === 0 ? ( <div className='row' /> ) : ( null ) }
                            <div className="col-12 col-md-6 pad-1 text-black text-left" key={i}>
                                <h4>{feed.title}</h4>
                                <p>{feed.text}</p>
                                <p className="margin-0"><b>Venue:</b> {feed.venue}</p>
                                <p><b>Date:</b> {feed.date}</p>
                                <a href={feed.link} target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i> Link</a>
                            </div>
                            </div>
                        )}
                    </div>
                    <a className="carousel-control-prev" href="#carouselExampleControls" role="button" data-slide="prev">
                        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                        <span className="sr-only">Previous</span>
                    </a>
                    <a className="carousel-control-next" href="#carouselExampleControls" role="button" data-slide="next">
                        <span className="carousel-control-next-icon" aria-hidden="true"></span>
                        <span className="sr-only">Next</span>
                    </a>
                </div> */}
            </section>
        )
    }
}

export default Feed;


