import React, { Component } from 'react';
import InitialState from '../../data/initialState';

class Projects extends Component {
    constructor(props){
        super(props);
        this.state = { projects: InitialState.sections.projects, currProject: null }
        this.handleProjectClick = this.handleProjectClick.bind(this);
    }

    handleProjectClick(projectId, event) {
        console.log(this.state.projects[projectId].image)
        this.setState({ currProject: this.state.projects[projectId] })
    }

    render() {
        return(
        <section className="bg-off-white" id="projects">
            
            <h1 className="text-accent"><i className="fas fa-project-diagram"></i> &nbsp;Projects</h1>
            <div className="container">
                <div className="row mt-5" data-toggle="modal" data-target="#projectModal" style={{ cursor: "pointer" }}>
                {this.state.projects.map((project) => { 
                    return (
                    <div className="col-12 col-md-6 hover-card pad-2 sr-icons" key={project.title} onClick={(e) => this.handleProjectClick(project.id, e)}>
                    <div>
                        <h3 className="text-left">{project.title}</h3>
                        <hr className="hr-almost-black"></hr>
                        <p className="text-accent text-left margin-0">Descripition :</p>
                        <ul>
                        { project.text.map((textBullet)=>
                            <li className="text-left margin-0" key={textBullet.length}>
                                {textBullet}
                            </li>
                        )}
                        </ul>
                        <p className="text-left text-grey-dark margin-0 mt-3"><i className="fas fa-calendar-alt text-accent"></i>&nbsp; {project.date}</p>
                        <p className="text-left text-grey-dark"><i className="fas fa-code text-accent"></i>&nbsp; {project.technologies}</p>  
                        </div>
                    </div>
                    ) 
                })}
                </div>
                <h5 className="text-center">and more...</h5>
            </div>

            <div className="modal fade" id="projectModal" tabIndex="-1" role="dialog" aria-labelledby="projectModalLabel" aria-hidden="true">
            <div className="modal-dialog modal-lg modal-dialog-centered" role="document">
                <div className="modal-content">
                <div className="modal-header text-center">
                    <h3 className="modal-title" id="projectModalLabel">{!this.state.currProject ? null : this.state.currProject.title}</h3>
                    <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                    <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div className="modal-body">
                    {
                        this.state.currProject
                        ? <div>{this.state.currProject.image ? <img src={"/assets/images/projects/"+this.state.currProject.image}  className="project-img" /> : null} <h5 className="text-left mt-3"><u>Description: </u></h5> <p className="text-left">{this.state.currProject.description}</p> {this.state.currProject.link ? <a href={this.state.currProject.link} target="_blank" rel="noopener noreferrer"><i className="fas fa-external-link-alt"></i> Link</a>: null } </div>
                        : null 
                    }
                </div>
                </div>
            </div>
            </div>
        </section>
        )
    }
}

export default Projects;


